body {
    background-color: black;
}
.drmTypeBox {
    background-color: lightgrey;
    width: 200px;
    height: 60px;
    border: 5px solid blue;
    margin: 10px;  
    text-align: center; 
}

.EME { 
    color: yellow;
    display: inline-block;
    vertical-align: middle;
    line-height: normal;
    position: relative;
    font-size: large;
    width: 900px;
    border: 5px yellow;
    margin: 10px;  
    text-align: center;
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(auto-fit, 260px);
}

.drmSubKey {
    font-size: small;
    border: 1px yellow;
    padding: 5px;
}

.DRMKeySystem {
    width: 100%;
}

.MemoryStat {
    color: blue;
}

.error {
    color: yellow;
}